import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import { Link, useNavigate, useParams } from "react-router-dom";

// needed styles
import pageStyles from "./index.module.scss";

import { useDispatch, useSelector } from "react-redux";
import { Container } from "react-bootstrap";
import { bookTripApi } from "api/trips";
import { async } from "@firebase/util";

const BookTripPage = () => {
  let navigate = useNavigate();
  const { tripId } = useParams();

  const dispatch = useDispatch();
  const { error, loading, umraTrips, allTrips, hajjTrips } =
    useSelector((state) => state.trips) || {};

  const { register, handleSubmit, reset } = useForm({
    shouldUseNativeValidation: true,
  });

  // TODO   handle the login method
  const submitHandler = async (data) => {
    data["trip_id"] = tripId;
    data["start_trip_date"] = data["start_booking_date"];
    try {
      const response = await bookTripApi(data);
      window.location.href = response;
    } catch (error) {
      console.log(error, "error");
    }
    // const { email, password } = data || {};
    // dispatch(signInUser({ email, password }));

    // reset();
  };

  return (
    <Container className="py-5">
      <form onSubmit={handleSubmit(submitHandler)} className={pageStyles.Form}>
        <h3 className="mb-3">حجز الرحله</h3>

        <label htmlFor="start_booking_date">تاريخ بداية الحجز</label>
        <input
          name="start_booking_date"
          type="date"
          // className={`${errors?.["startDate"] ? styles.errorField : ""} `}
          placeholder="تاريخ بداية الحجز"
          {...register("start_booking_date", { required: true })}
        />
        <label htmlFor="number_of_persons">
          <input
            type="number"
            name="number_of_persons"
            id="number_of_persons"
            placeholder="عدد الأشخاص"
            {...register("number_of_persons", { required: true })}
          />
          <div></div>
        </label>
        <label htmlFor="notes">
          <textarea
            cols={15}
            rows={15}
            name="notes"
            id="notes"
            placeholder="ملاحظات"
            {...register("notes", { required: false })}
          />
        </label>

        <input type="submit" value="حجز" />
      </form>
    </Container>
  );
};

export default BookTripPage;
